.title {
  margin: 0;

  &--h1 {
    font-size: 48px;

    @include tablet {
      font-size: 32px;
    }
  }

  &--h2 {
    font-size: 32px;

    @include tablet {
      font-size: 24px;
    }
  }

  &--h3 {
    font-size: 24px;

    @include tablet {
      font-size: 20px;
    }
  }

  &--h4 {
    font-size: 20px;

    @include tablet {
      font-size: 18px;
    }
  }
}
